import "./Rating.scss";

import TextHeader from "../Common/TextHeader/TextHeader";

export default function Rating({}) {
  const ratingPlaces = [
    { place: "1 ", payment: "3 TON" },
    { place: "2 ", payment: "1.5 TON" },
    { place: "3 ", payment: "0.8 TON" },
    { place: "4-10 ", payment: "0.2 TON" },
    { place: "11-50 ", payment: "0.1 TON" },
  ];
  const ratingPlayers = [
    {
      playerName: " ",
      countToday: "23542441",
      countAll: "992104429",
    },
    {
      playerName: " ",
      countToday: "23542441",
      countAll: "992104429",
    },
    {
      playerName: " ",
      countToday: "23542441",
      countAll: "992104429",
    },
    {
      playerName: " ",
      countToday: "23542441",
      countAll: "992104429",
    },
    {
      playerName: " ",
      countToday: "23542441",
      countAll: "992104429",
    },
  ];
  return (
    <section className="block-white">
      <TextHeader title={[<span>  </span>]} />
      <p>   20:00 -50     TON</p>
      <article className="rating-places">
        {ratingPlaces.map(({ place, payment }) => {
          return (
            <div>
              <p>{place}</p>
              <p>{payment}</p>
            </div>
          );
        })}
      </article>
      <p>
             20:00  .     
          -50,         
        TON .
      </p>
      <p>
         00:00        
        
      </p>
      <article className="rating-players">
        {ratingPlayers.map(({ playerName, countToday, countAll }, i) => {
          return (
            <div className="rating-players__player">
              <div className="rating-players__place">{++i}</div>
              <h3>{playerName}</h3>
              <div className="rating-players__count">
                <p>{countToday}</p>
                <p>{countAll}</p>
              </div>
            </div>
          );
        })}
      </article>
      <button className="btn-ad">
        <div className="btn-ad__indicator"></div>
        <div className="btn-ad__text">
          <h3></h3>
          <p> </p>
        </div>
        <div className="btn-ad__ad">ad</div>
      </button>
    </section>
  );
}
