import "./SuperClick.scss";

import Tabs from "../Common/Tabs/Tabs";
import Task from "../Common/Task/Task";
import Partner from "../Common/Partner/Partner";

import loanImg from "../../img/loan/bg.png";
import partnerImg from "../../img/loan/more.png";
import sponsorLogo from "../../img/gameSport/logo.png";
import sponsorBg from "../../img/gameSport/bg.png";
import cancel from "../../img/svgicons/cancel.svg";
import coin from "../../img/svgicons/coin.svg";

export default function SuperClick({}) {
  const tabsOtherSubs = [
    <>
      <p>
                .
         ,      .
      </p>
      <Task
        title=""
        tasks={[
          {
            taskType: "google",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
            isNew: true,
          },
          {
            taskType: "google",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
            isNew: true,
          },
          {
            taskType: "star",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
          },
        ]}
      />
    </>,
  ];
  const tabsOtherServices = [
    <>
      <p>
                .
         ,      .
      </p>
      <Task
        title=""
        tasks={[
          {
            taskType: "google",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
            isNew: true,
            color: "purple",
          },
          {
            taskType: "google",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
            isNew: true,
            color: "purple",
          },
          {
            taskType: "star",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
            color: "purple",
          },
        ]}
      />
    </>,
  ];
  const tabsOtherPartners = [
    <>
      <p>
                .
         ,      .
      </p>
      <Task
        title=""
        tasks={[
          {
            taskType: "google",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
            isNew: true,
            color: "green",
          },
          {
            taskType: "google",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
            isNew: true,
            color: "green",
          },
          {
            taskType: "star",
            taskTitle: "     Google",
            taskText: "+10 000 000 ",
            color: "green",
          },
        ]}
      />
    </>,
  ];
  const tabsOtherTabs = [tabsOtherSubs, tabsOtherServices, tabsOtherPartners];
  const tabsOtherLinks = ["", "", " "];

  const tabsOther = [
    <section className="superclick-tabs">
      <Tabs tabsLinks={tabsOtherLinks} tabsContent={tabsOtherTabs} />
    </section>,
  ];
  const tabsLoan = [
    <>
      <header className="tabs-loan__header">
        <h2></h2>
        <p>   15    3 000 000 </p>
      </header>
      <img src={loanImg} alt="loanImg" />
      <p>
                .
         ,      .
      </p>
      <Partner
        title=" "
        partners={[
          {
            img: partnerImg,
            partnerTitle: "",
            partnerSubtitle: "  95%",
            partnerText: [
              <>
                <p>
                    <span> 3000  30000</span>
                </p>
                <p>
                    <span> 7  30 </span>
                </p>
                <p>
                   <span> 18 </span>
                </p>
                <p>
                   <span> 0%</span>
                </p>
              </>,
            ],
            addClicks: "+ 3 000 000 ",
            addCoins: "+1",
          },
        ]}
      />
    </>,
  ];
  const tabsToken = [
    <div className="sponsor">
      <h2>GameSport</h2>
      <div className="sponsor-block">
        <div className="sponsor-block__img">
          <img src={sponsorLogo} alt="sponsorLogo" />
        </div>
        <p>GameSport     </p>
      </div>
      <div className="sponsor-bg">
        <img src={sponsorBg} alt="sponsorBg" />
      </div>
      <p>
              5    1  
          150 000  .
      </p>
      <div className="sponsor-sale block-with-bg">5  = 1</div>
      <p>
           PRO-    
          .
      </p>
      <div className="sponsor-statement">
        <img src={cancel} alt="cancel" />
        <img src={coin} alt="coin" />
        <p>+ 1 000 000</p>
      </div>
      <button className="btn btn-gradient"></button>
      <button className="btn-bordered btn-gradient">
        <p> </p>
      </button>
      <div className="sponsor-task red">  </div>
    </div>,
  ];

  const tabsLinks = ["", "", ""];
  const tabsContent = [tabsOther, tabsLoan, tabsToken];
  return (
    <section className="sponsor-tabs block-white">
      <Tabs
        tabsLinks={tabsLinks}
        tabsContent={tabsContent}
        tabsColor="purple"
      />
    </section>
  );
}
