import "./Boost.scss";

import Tabs from "../Common/Tabs/Tabs";
import Task from "../Common/Task/Task";
import TextHeader from "../Common/TextHeader/TextHeader";

import tipImg from "../../img/svgicons/tip.svg";

export default function Boost({}) {
  const tabMoreClick = [
    <Task
      title="-"
      tasks={[
        {
          taskType: "star",
          taskTitle: "-",
          taskText: "  ,   ",
          isAd: true,
        },
      ]}
    />,
    <Task
      title=" "
      tasks={[
        {
          taskType: "google",
          taskTitle: "     Google",
          taskText: "+10 000 000 ",
          isNew: true,
        },
        {
          taskType: "google",
          taskTitle: "      Google",
          taskText: "+100 000 ",
          isNew: true,
        },
        {
          taskType: "star",
          taskTitle: "  Candy Defense -  $DEFENSE  ",
          taskText: "+30 000 ",
        },
      ]}
    />,
    <Task
      title=" "
      subTitle="        "
      tasks={[
        {
          taskType: "google",
          taskTitle: "     Google",
          taskText: "+10 000 000 ",
          isNew: true,
          color: "purple",
        },
        {
          taskType: "google",
          taskTitle: "      Google",
          taskText: "+100 000 ",
          color: "purple",
        },
        {
          taskType: "star",
          taskTitle: "  Candy Defense -  $DEFENSE  ",
          taskText: "+30 000 ",
          color: "purple",
        },
      ]}
    />,
  ];
  const tabGoogleTask = [
    <div className="block-with-bg">
      <Task title="  " />
      <div className="count-block">
        <p>100 000</p>
        <p></p>
      </div>

      <p>
           ,     ,  
        ,     .
      </p>

      <div className="tip">
        <img src={tipImg} alt="tipImg" />
        <p>
          <b>   !</b>    ,  
                .
        </p>
      </div>
    </div>,
    <Task
      tasks={[
        {
          taskType: "google",
          taskTitle: "     Google",
          taskText: "+10 000 000 ",
          isNew: true,
        },
        {
          taskType: "google",
          taskTitle: "      Google",
          taskText: "+100 000 ",
        },
        {
          taskType: "star",
          taskTitle: "  Candy Defense -  $DEFENSE  ",
          taskText: "+30 000 ",
        },
      ]}
    />,
  ];
  const tabsLinks = [
    "  ",
    "    ",
    "Google ",
    "  ",
  ];
  const tabsContent = [
    tabMoreClick,
    "    ",
    tabGoogleTask,
    "  ",
  ];
  return (
    <section className="block-white">
      <TextHeader
        title={[
          <>
            <span>  </span> -   ,  
            TON.
          </>,
        ]}
        subTitle={"     TON    !"}
      />
      <Tabs tabsLinks={tabsLinks} tabsContent={tabsContent} />
    </section>
  );
}
